// src/components/ProductCard.js
import Link from "next/link";

export default function ProductCard({ product }) {
  const price = product.price ?? 0;
  return (
    <div style={{ border:"1px solid #ddd", borderRadius:6, padding:12, width:260 }}>
      <div style={{ height:140, marginBottom:8, background:"#f6f6f6", display:"flex", alignItems:"center", justifyContent:"center" }}>
        <img src={(product.images && product.images[0]) || "/images/placeholder.png"} alt={product.name}
             style={{ maxHeight:130, maxWidth:"100%", objectFit:"cover" }} />
      </div>
      <h3 style={{ margin:"6px 0" }}>{product.name}</h3>
      <div style={{ color:"#555", marginBottom:8 }}>{product.description || ""}</div>
      <div style={{ fontWeight:700, marginBottom:8 }}>₹{price}</div>
      <div style={{ display:"flex", justifyContent:"space-between" }}>
        <Link href={`/product/${product._id}`} style={{ textDecoration:"none" }}>View</Link>
      </div>
    </div>
  );
}
